'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Sidebar from '@/components/Sidebar';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import AdRotator from '@/components/AdRotator';
import { FiCalendar, FiUser, FiFileText, FiClock } from 'react-icons/fi';

export default function DashboardPage() {
  const { user, loading: authLoading } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
    }
  }, [user, authLoading, router]);

  useEffect(() => {
    if (user) {
      if (user.role === 'doctor') {
        checkDoctorProfile();
        // Redirect doctors to appointments page (doctor portal)
        router.push('/appointments');
        return;
      } else {
        // Force fresh data fetch for patients
        fetchDashboardData();
      }
    }
  }, [user]);

  const checkDoctorProfile = async () => {
    try {
      const response = await api.get('/doctors/profile/me');
      if (!response.data.doctor || response.data.doctor.status === 'pending') {
        // Redirect to complete profile if not completed or pending
        router.push('/doctor/complete-profile');
      } else if (response.data.doctor.status === 'approved') {
        fetchDashboardData();
      }
    } catch (error) {
      // If no profile exists, redirect to complete profile
      router.push('/doctor/complete-profile');
    }
  };

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      if (user?.role === 'admin') {
        const response = await api.get('/admin/dashboard');
        setStats(response.data);
      } else if (user?.role === 'doctor') {
        const appointments = await api.get('/appointments?limit=5');
        const prescriptions = await api.get('/prescriptions?limit=5');
        setStats({
          appointments: appointments.data.appointments,
          prescriptions: prescriptions.data.prescriptions,
        });
      } else {
        // Fetch patient's own appointments and prescriptions with fresh data
        const appointments = await api.get(`/appointments?patientId=${user?.id}&limit=5`);
        const prescriptions = await api.get(`/prescriptions?limit=5`);
        setStats({
          appointments: appointments.data.appointments || [],
          prescriptions: prescriptions.data.prescriptions || [],
        });
      }
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
      // Set empty stats on error
      setStats({
        appointments: [],
        prescriptions: [],
      });
    } finally {
      setLoading(false);
    }
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user) {
    return null;
  }

  return (
    <div className="flex min-h-screen">
      <Sidebar />
      <main className="ml-64 flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-800 mb-6">
            Welcome back, {user.name}!
          </h1>

          {user.role === 'admin' && stats && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Total Users</p>
                    <p className="text-3xl font-bold text-teal-600 mt-2">{stats.stats?.totalUsers || 0}</p>
                  </div>
                  <FiUser className="text-4xl text-teal-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Total Doctors</p>
                    <p className="text-3xl font-bold text-indigo-600 mt-2">{stats.stats?.totalDoctors || 0}</p>
                  </div>
                  <FiUser className="text-4xl text-indigo-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Appointments</p>
                    <p className="text-3xl font-bold text-teal-600 mt-2">{stats.stats?.totalAppointments || 0}</p>
                  </div>
                  <FiCalendar className="text-4xl text-teal-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Pending</p>
                    <p className="text-3xl font-bold text-orange-600 mt-2">{stats.stats?.pendingAppointments || 0}</p>
                  </div>
                  <FiClock className="text-4xl text-orange-600" />
                </div>
              </div>
            </div>
          )}

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2">
              <div className="bg-white rounded-lg shadow-md p-6">
                <h2 className="text-xl font-semibold text-gray-800 mb-4">Recent Appointments</h2>
                {stats?.appointments && stats.appointments.length > 0 ? (
                  <div className="space-y-4">
                    {stats.appointments.map((appt: any) => (
                      <div
                        key={appt.id}
                        className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
                      >
                        <div className="flex justify-between items-start">
                          <div>
                            <p className="font-semibold text-gray-800">
                              {user.role === 'patient'
                                ? appt.doctor?.user?.name
                                : appt.patient?.name}
                            </p>
                            <p className="text-sm text-gray-600 mt-1">
                              {new Date(appt.appointmentDate).toLocaleDateString()} at {appt.appointmentTime}
                            </p>
                            {appt.reason && (
                              <p className="text-sm text-gray-500 mt-1">{appt.reason}</p>
                            )}
                          </div>
                          <span
                            className={`px-3 py-1 rounded-full text-xs font-semibold ${
                              appt.status === 'confirmed'
                                ? 'bg-green-100 text-green-800'
                                : appt.status === 'pending'
                                ? 'bg-yellow-100 text-yellow-800'
                                : appt.status === 'completed'
                                ? 'bg-blue-100 text-blue-800'
                                : 'bg-red-100 text-red-800'
                            }`}
                          >
                            {appt.status}
                          </span>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-gray-500 text-center py-8">No appointments yet</p>
                )}
              </div>
            </div>

            {user.role === 'doctor' && (
              <div>
                <AdRotator targetAudience="doctor" />
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}

